// src/styles/components/sections/intro-sections/_share.scss
@use '../../../base/variables' as *;
@use '../../../base/mixins' as *;

//	.share
.share {
	padding: 30px 0;
	background-color: $color-bg-gray;
	color: $color-text-dark;

	@include respond-to($breakpoint-sm) {
		padding: 25px 0px;
	}

	//.share__title-------------------------------
	&__title {
		font-size: 34px;
		font-weight: 900;
		line-height: 1.2;
		margin: 0px 0px 20px 0px;

		@include respond-to($breakpoint-sm) {
			font-size: 26px;
		}
	}

	//.share__subtitle-------------------------------
	&__subtitle {
		// Typography
		font-size: 20px;
		font-weight: 600;
		line-height: 1.43;
		max-width: 60rem;
		margin: 0px 0px 20px 0px;

		@include respond-to($breakpoint-sm) {
			font-size: 22px;
			line-height: 1.3;
		}
	}

	//	.share__list-------------------------------
	&__list {
		display: grid;
		grid-template-columns: auto 1fr;
		column-gap: 40px;
		row-gap: 10px;
		font-size: 18px;
		line-height: 1.4;
		margin: 0px 0px 40px 0px;

		@include respond-to($breakpoint-xs) {
			grid-template-columns: 1fr;
		}

		div:nth-child(2n) {
			font-weight: 600;
		}
	}

	//.share__block-------------------------------
	&__block {
		display: flex;
		gap: 20px;
	}

	//.share__button-------------------------------
	&__button {
		transition: all 0.3s ease;

		&:hover {
			transform: translateY(1px);
		}

		//
		img {
			width: 40px;
			height: 40px;
		}
	}
}